
import { defineComponent } from "vue"

const SWITCH_EVENT_NAME = "switch"

export default defineComponent({
  name: "SwitchButton",
  emits: [SWITCH_EVENT_NAME],
  components: {},
  
  props: {
    initial: Boolean,
    disabled: Boolean,
    large: Boolean,
  },

  data() {
    return {
      checked: this.initial,
    }
  },

  methods: {
    onSwitchClicked() {
      if (this.disabled) {
        this.checked = !this.checked
        return
      }

      this.$emit(SWITCH_EVENT_NAME, this.checked)
    }
  }
})
